.App,
.root {
    height: 100%;
}
.App-header {
    width: 100%;
    text-align: center;
    padding: 20px 0 10px;
    box-sizing: border-box;
}
label {
    min-height: 19px;
    font-size: 13px;
    font-weight: 700;
    margin-bottom: 4px;
    display: block;
}
input {
    box-sizing: border-box;
    width: 100%;
    padding: 10px 14px;
    border-radius: 4px;
    font-size: 14px;
    border: 1px solid rgba(34, 36, 38, 0.15);
}

.field {
    margin-bottom: 14px;
}
.double-input {
    display: flex;
    position: relative;
    justify-content: space-between;
}
.left-db-input {
    width: 30%;
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    text-align: center;
}
.right-db-input {
    width: 70%;
    padding-left: 30px;
    border-left: none;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    text-align: center;
}
.double-input::before {
    content: '-';
    display: block;
    position: absolute;
    top: 10px;
    left: 30%;
}
.eur,
.ct {
    width: 48%;
    padding-right: 35px;
}
.double-input.price::before {
    content: 'Eur';
    left: 39%;
}
.double-input.price::after {
    content: 'ct';
    display: block;
    position: absolute;
    top: 10px;
    right: 3%;
}
.main-form {
    width: 90%;
    margin: auto;
    max-width: 1000px;
}
.form-inner {
    flex-wrap: wrap;
    display: flex;
}
.form-column {
    box-sizing: border-box;
    flex: 1;
    padding: 20px;
    margin: auto;
    min-width: 350px;
    max-width: 420px;
    display: flex;
    flex-direction: column;
}
.inputs-devider {
    margin-top: 30px;
}
.calendar-modal {
    z-index: 20;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
}
.calendar {
    z-index: 1;
    margin: auto;
    padding: 20px;
}
.calendar-backdrop {
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.buttons-container {
    display: flex;
    padding: 20px 0;
    justify-content: space-evenly;
}
.btn {
    background-color: #2185d0;
    color: #fff;
    font-size: 14px;
    border: 1px solid #fff;
    padding: 11px 21px;
    font-weight: 700;
    border-radius: 4px;
}
.btn.secondary {
    background: #1b1c1d;
}
th,
td {
    border: 1px solid;
    padding: 5px;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
